import React, { Component } from 'react';

import { Translation } from 'react-i18next';

import { TimelineMax, TweenLite } from 'gsap/all';
import ScrollMagic from 'scrollmagic';

import _throttle from 'lodash.throttle';

// import 'scrollmagic/scrollmagic/uncompressed/plugins/debug.addIndicators.js';

import Hero from 'sections/Hero/Hero';
import TextBlock from 'sections/TextBlock/TextBlock';
import Demo from 'sections/Demo/Demo';
import Video from 'sections/Video/Video';
import Benefits from 'sections/Benefits/Benefits';
import Roadmap from 'sections/Roadmap/Roadmap';
import Reviews from 'sections/Reviews/Reviews';
import JoinUs from 'sections/JoinUs/JoinUs';

export default class Home extends Component {
  constructor() {
    super();

    this.classes = {
      roadmap: {
        container: '.js-roadmap',
        headline: '.js-roadmap-headline'
      },
      video: {
        curtain: '.js-video-curtain',
        content: '.js-video-content',
        shadow: '.js-video-shadow'
      },
      benefits: {
        items: '.js-benefits-item'
      }
    };

    this.directions = {
      forward: 'FORWARD',
      reverse: 'REVERSE'
    };

    this.controller = new ScrollMagic.Controller({
      addIndicators: true
    });

    this.getVideoTl = this.getVideoTl.bind(this);
    this.getBenefitsTl = this.getBenefitsTl.bind(this);
  }

  handleResize() {
    this.scenes.forEach(item => item
      .refresh()
      .update(true)
    );
    this.controller.update(true);
  }

  destroy() {
    this.controller.destroy(true);
    this.scenes.forEach(item => item.destroy(true));
  }

  componentDidUpdate() {
    this.scenes.forEach(item => item.destroy(true));
    this.createSceneAnimations();
    this.controller.update(true);

    this.controller.scrollTo(0);
  }

  componentWillUnmount() {
    this.destroy();
  }

  componentDidMount() {
    this.isTablet = window.innerWidth <= 1024;

    this.createSceneAnimations();
    window.addEventListener('resize', _throttle(this.handleResize.bind(this), 200));
  }

  getBenefitsTl(container) {
    const {
      items
    } = this.classes.benefits;

    const video = container.querySelector('video');

    return new TimelineMax({
      paused: true
    })
      .staggerFromTo(items, 1, { autoAlpha: 0, y: '30%' }, { autoAlpha: 1, y: '0%', className: '+=is-animated' }, 2)
      .staggerTo(items, 1, { autoAlpha: 0, y: '-30%', className: '-=is-animated' }, 2, 1)
      .fromTo(
        video,
        0.2,
        {
          autoAlpha: 0
        },
        {
          autoAlpha: 1,
          onComplete: () => {
            if (video.getAttribute('src')) return;

            const { src } = video.dataset;
            video.setAttribute('src', src);
          }
        },
        '-=1.5'
      )
    ;
  }

  getVideoTl() {
    const {
      curtain,
      content,
      shadow
    } = this.classes.video;

    const curtains = [...document.querySelectorAll(curtain)];

    return new TimelineMax({
      paused: true
    })
      .to(curtains[0], 1, { y: '-100%' })
      .to(curtains[1], 1, { y: '100%' }, 0)
      .to(curtains[2], 1, { x: '-100%' }, 0)
      .to(curtains[3], 1, { x: '100%' }, 0)
      .fromTo(shadow, 1, { autoAlpha: 0 }, { autoAlpha: 1 }, '-=0.2')
      .fromTo(content, 2, { y: window.innerHeight }, { y: 0 }, '-=0.5')
    ;
  }

  roadmapOnEnter() {
    const {
      headline: headlineClassName
    } = this.classes.roadmap;

    const headline = document.querySelector(headlineClassName);

    const {
      top,
      left
    } = headline.getBoundingClientRect();

    TweenLite.set(headlineClassName, { position: 'fixed', top, left, y: 0 });
  }

  roadmapOnLeave({ scrollDirection }) {
    const {
      headline: headlineClassName,
      container: containerClassName
    } = this.classes.roadmap;

    const container = document.querySelector(containerClassName);
    const headline = document.querySelector(headlineClassName);

    if (scrollDirection === this.directions.forward) {
      const y = container.children[0].offsetHeight - headline.offsetHeight;
      TweenLite.set(headline, { position: 'relative', top: 0, left: 0, y });
    } else {
      TweenLite.set(headline, { position: 'relative', top: 0, left: 0 });
    }
  }

  getAnimation(name, container) {
    return ({
      roadmap: {
        durationCoef: 0.87,
        onEnter: this.roadmapOnEnter,
        onLeave: this.roadmapOnLeave,
        trigerHook: 0.01
      },
      video: {
        durationCoef: 2,
        pin: true,
        trigerHook: 0.001,
        tl: this.getVideoTl,
      },
      benefits: {
        durationCoef: 12,
        pin: true,
        trigerHook: 0.001,
        tl: this.getBenefitsTl.bind(this, container)
      }
    }[name]);
  }

  createSceneAnimations() {
    const containers = [...document.querySelectorAll('.js-scene')];

    this.scenes = containers.map((el) => {
      const { sceneName } = el.dataset;
      const animation = this.getAnimation(sceneName, el);

      const {
        durationCoef,
        trigerHook,
        pin,
        onEnter,
        onLeave
      } = animation;

      const tl = animation.tl;

      const { offsetHeight } = el;
      const duration = durationCoef ? offsetHeight * durationCoef : offsetHeight;

      const scene =  new ScrollMagic.Scene({
        duration,
        triggerElement: el,
        triggerHook: trigerHook || 0.5
      })
        // .addIndicators({
        //   name: sceneName
        // })
      ;

      if (tl) scene.setTween(tl().play());
      if (pin) scene.setPin(el, { pushFollowers: true });

      if (!this.isTablet) {
        if (onEnter) scene.on('enter', onEnter.bind(this));
        if (onLeave) scene.on('leave', onLeave.bind(this));  
      };

      scene.addTo(this.controller);
      return scene;
    });
  }

  render() {
    return (
      <Translation>
      { 
        t => (
          <div className="home">
            <Hero
              title={t('pages.home.hero.title')}
              subtitle={t('pages.home.hero.subtitle')}
              text={t('pages.home.hero.text')}
              btn={t('pages.home.hero.btn', { returnObjects: true })}
            />
            <TextBlock text={t('pages.home.about')} />
            <Demo
              items={t('pages.home.demo.items', { returnObjects: true })}
              footer={t('pages.home.demo.footer')}
              interface={t('pages.home.demo.interface', { returnObjects: true })}
            />
            <div id="advantages">
              <Video
                headline={t('pages.home.video.headline')}
                text={t('pages.home.video.text')}
                src="video/1025767460-preview.mp4"
              />
            </div>
            <Benefits items={t('pages.home.benefits', { returnObjects: true })} />
            <Roadmap 
              headline={t('pages.home.roadmap.headline')}
              items={t('pages.home.roadmap.items', { returnObjects: true })}
              active={t('pages.home.roadmap.active')}
            />
            <Reviews slides={t('pages.home.reviews.slides', { returnObjects: true })} />
            <JoinUs
              headline={t('pages.home.join.headline')}
              subscribe={t('pages.home.join.subscribe', { returnObjects: true })}
              partner={t('pages.home.join.partner', { returnObjects: true })}
              comunity={t('pages.home.join.comunity', { returnObjects: true })}
            />
          </div>
        )
      }
      </Translation>
    )
  }
};
