import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';

import { TweenLite } from 'gsap/all';

import classNames from 'classnames';

import Tooltip from 'components/Tootlip/Tooltip';

import './Nav.sass';

export default class Nav extends Component {
  constructor() {
    super();

    this.isScrolling = false;
  }

  componentDidMount() {
    [...document.querySelectorAll('.js-scroll-to')]
      .forEach(item => item.addEventListener('click', this.handleScrollTo.bind(this)));
  }

  handleScrollTo({ target }) {
    if (this.isScrolling) return;
    this.isScrolling = true;

    const id = target.getAttribute('href');
    const section = document.querySelector(id);

    if (!section) return;

    const { offsetTop } = section;
    TweenLite.to(window, 2, { scrollTo: offsetTop, onComplete: () => this.isScrolling = false });
  }

  insertTooltip(value) {
    return <Tooltip value={value} className="nav__tooltip" />;
  }

  createTooltipLink(item) {
    return (
      <span 
        className="nav__item" 
        key={item.title}
      >
        {item.title}
        {item.tooltip ? this.insertTooltip(item.tooltip) : null}
      </span>
    );
  }

  renderNavItem(item) {
    if (!item.url) return this.createTooltipLink(item);

    // @TODO: remove
    if (item.url.indexOf('about') !== -1) return null;

    return item.url.charAt(0) === '/' ?
      <NavLink className="nav__item" activeClassName="is-active" to={item.url} key={item.title}>
        {item.title}
      </NavLink>
      :
        <a
          className={classNames("nav__item", { "js-scroll-to": item.url.charAt(0) === '#' })}
          href={item.url}
          key={item.title}
          target={item.isExternal ? '_blank' : null}
          rel={item.isExternal ? 'noopener noreferrer' : null}
        >
          {item.title}
          {item.tooltip ? this.insertTooltip(item.tooltip) : null}
        </a>
      ;
  }

  render() {
    const { links } = this.props;

    return (
      <nav
        className={classNames('nav', { 'nav_dark': this.props.isDark })}
      >
        {links.map(item => this.renderNavItem(item))}
      </nav>
    );
  }
};
