import React, { Component } from 'react';
import { Translation } from 'react-i18next';
import i18n from 'i18next';

import {
  BrowserRouter as Router,
  Switch,
  Route
} from 'react-router-dom';

import classNames from 'classnames';

import _throttle from 'lodash.throttle';

import Header from 'components/Header/Header';
import Footer from 'components/Footer/Footer';

import Home from 'pages/Home/Home';

import { updateViewportUnits } from 'Utils';

export default class App extends Component {
  handleResize() {
    updateViewportUnits();
  }

  componentDidMount() {
    updateViewportUnits();
    window.addEventListener('resize', _throttle(this.handleResize.bind(this), 200));
  }

  render() {
    return (
      <Translation>
        { 
          t => (
            <Router>
              <div className={classNames('app', { [`is-${i18n.language}`]: i18n.language })}>
                <Header links={t('nav', { returnObjects: true })} />
                  <div className="app__body">
                    <Switch>
                      <Route path="*" component={Home} exact />
                      <Route path="/about">About</Route>
                    </Switch>
                  </div>
                <Footer links={t('footer.links', { returnObjects: true })} />
              </div>
            </Router>
          )
        }
      </Translation>
    );
  }
};
